import axios from "axios";

export default(url, data={}, method="GET")=>{
    return new Promise( async (resolve, reject)=>{
        let promiseAxios;
        
        if(method === "GET"){
            // params é…ç½®æŒ‡å®šçš„æ˜¯ query å‚æ•°
            promiseAxios = axios.get(url, {params:data}, "GET");
        }else if(method === "POST"){
            promiseAxios = axios.post(url, data, "POST");
        }else{
            reject("ajax.js ---- method ä¼ å‚é”™è¯¯");
        }
        
        promiseAxios.then(result=>{
            resolve(result.data);
        }).catch(error=>{
            reject("ajax.js ---- è¯·æ±‚é”™è¯¯");
        })
    })
}
