export function getCurKey(pathname, menu) {
    let i=0;
    
    let result = {
        preKey: null,
        key: null
    };
    
    for(i=0; i<menu.length; i++){
        result.preKey = menu[i];
        if(result.preKey.key === pathname){
            result.key = menu[i];
            return result;
        }else if(menu[i].children){
            let ret = getCurKey(pathname, menu[i].children);
            if(ret && ret.key){
                ret.preKey = menu[i];
                return ret;
            }
        }
    }
    
    return false;
}

